*{
    box-sizing: border-box;
}
.model, .modelAlbum1, .modelAlbum2 {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(139, 139, 139);
    z-index: 999;
    visibility: hidden;
    opacity: 0;
    transform: scale(0); 
    transition: opacity 0.4s ease, visibility 0.5s ease, transform 0.4s ease-in-out;
    overflow: hidden;
}
.model.open, .modelAlbum1.open, .modelAlbum2.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}
.player{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    padding: 20px;
    border-radius: 10px;
}
.player h3 {
    color: white;
    margin: 5px;
    text-align: center;
}
.player audio{
    margin: 10px 0 30px;
}
.player img {
    border-radius: 5px;
    object-fit: cover;
}
.skip {
    display: flex;
    width: 100px;
    justify-content: space-evenly;
}
.imageAlbum {
    display: flex;    
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.imageAlbum img {
    border-radius: 20px;
    background-size: cover;
    background-position: center center;
    padding: 10px;
    width: 250px;
    height: 250px;
}
@media screen and (max-width:259px) {
    .imageAlbum img {
        width: 200px;
        height: 200px;
    }
    
}